$color-main: #f3a01e;
$color-dark: #4399d4;

.maintenance-section {
  padding: 40px 0;
  .maintenance-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 20px;
    .image-box {
      height: 350px;
      justify-content: center;
      align-items: center;
      img {
        object-fit: cover;
        height: 100%;
        width: 100%;
      }
    }
    .title-box {
      display: flex;
      flex-direction: row;
      margin-top: 25px;
      margin-bottom: 6px;
      h4 {
        font-family: "Roboto-Medium";
        font-size: 20px;
      }
      label {
        margin-left: 20px;
        cursor: pointer;
        body.ar & {
          margin-left: unset;
          margin-right: 20px;
        }
        position: relative;
        margin-bottom: 8px;
        color: $color-main;
        i {
          font-size: 17px;
          transition: 0.3s;
          opacity: 1;
          backface-visibility: hidden;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        i.fa-check-square {
          opacity: 0;
          transition: 0.5s;
        }
      }
      input {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        &:checked ~ label i {
          opacity: 0;
        }
        &:checked ~ label i.fa-check-square {
          opacity: 1;
        }
      }
    }
    .maintenance-sup-title {
      font-family: "Roboto-Medium";
      font-size: 20px;
    }
    .price {
      color: $color-dark;
      font-family: "Roboto-Bold";
      font-size: 20px;
    }
  }
}
